<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- table -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label="" width="200px"></el-table-column>
        <el-table-column prop="order_price" label="()"></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.pay_status === '1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column prop="update_time" label="">
          <template slot-scope="scope">{{scope.row.create_time | dateFormate}}</template>
        </el-table-column>
        <el-table-column label="">
          <template >
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-edit"
                @click="editDialogVisible = true"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button
                size="mini"
                type="success"
                icon="el-icon-location"
                @click="showexpressDialog"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 7, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="clearForm">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader v-model="editForm.address1" :options="cityData" :props="editProps"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="expressDialogVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in expressInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 3
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      editDialogVisible: false,
      editForm: {
        address1: [],
        address2: ''
      },
      // 
      editFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      editProps: {
        expandTrigger: 'hover'
      },
      // 
      cityData,
      // 
      expressDialogVisible: false,
      // 
      expressInfo: {}
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList() {
      const { data: res } = await this.$http.get(`orders`, {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.orderList = res.data.goods
      this.total = res.data.total
      this.$message.success('')
      console.log(this.orderList)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    // 
    clearForm() {
      this.$refs.editFormRef.resetFields()
    },
    async showexpressDialog() {
      const { data: res } = await this.$http.get(`/kuaidi/1106975712662`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.expressInfo = res.data
      this.expressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';
.el-cascader {
  width: 100%;
}
</style>
